import { useContext, useEffect, useState } from "react";
import Figure from "react-bootstrap/Figure";
import { MongoContext } from "../context/mongo-context";

function ImageMeta(props) {
  const [loading, setLoading] = useState(true);
  const [art, setArt] = useState({});

  const { user, getImageData, addToImageMap } = useContext(MongoContext);

  useEffect(() => {
    const a = getImageData(props.sid);
    if (!a) {
      setLoading(true);
      user.functions.fetchByID(props.sid).then((resp) => {
        if (resp.result) {
          addToImageMap(props.sid, resp.result);
          setArt(resp.result);
          setLoading(false);
        }
      });
    } else {
      setArt(a);
      setLoading(false);
    }
  }, []);
  return art ? (
    <>
      <Figure.Caption>Brightness : {art.brightness}</Figure.Caption>
      <Figure.Caption>
        <div
          className="color-box"
          style={{ backgroundColor: `#${art.dom_color}` }}
        ></div>
      </Figure.Caption>
    </>
  ) : null;
}

export default ImageMeta;
